body
{
  background-color:darkolivegreen;
}

.file {
  display: flex;
  justify-content: center;
  margin-top: 170px;
  

}

.signup-form {
  background-color:black;
  color: white;
  border: 3px solid black;
  border-radius: 10px;
  width: 480px;
  height:230px;
  text-align: center;
}

.signup {
  text-align: left;
  padding-bottom: 10px;
  display: flex;
}
.signup label {
  /* font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif; */
  margin-left: 5px;
  flex: 1;
}

.signup input {
  flex: 3.5;
  margin-right: 10px;
  background-color: bisque;
  border-radius: 5px;
}
.label {
  text-align: left;
  padding-bottom: 10px;
  display: flex;
}

.label label {
  margin-left: 5px;
  flex: 1;
}

.label input {
  flex: 3.5;
  margin-right: 10px;
  background-color: bisque;
  border-radius: 5px;
}

.child {
  text-align: left;
  padding-bottom: 10px;
  display: flex;
}

.child label {
  margin-left: 2px;
  flex: 1;
}

.child input {
  margin-right: 10px;
  flex: 3.5;
  height: 16px;
  background-color: bisque;
  border-radius: 5px;
}