.cal {
  background-color: rgb(68, 59, 59);
  border: 3px solid black;
  height: 380px;
  width: 340px;
  text-align: center;
  margin: auto;
  padding: 5px;
  margin-top: 50px;
  margin-bottom: 100px;
}

.cal button {
  margin: 2px;
  width: 75px;
  height: 65px;
  padding: 05px;
  background-color: azure;
}

.cal input {
  opacity: 0.8;
  width: 300px;
  height: 65px;
  padding: 2px;
  border: 2px solid black;
  margin: 3px;
  margin-bottom: 25px;
}

.cal:hover {
  transition: all 0.7s;
  transform: scale(1.1);
}
