.form {
  display: flex;
  justify-content: center;
  margin-top: 170px;
}
.login {
border: 2px solid black;
border-radius: 5px;
width:380px;
height: 170px;
text-align: center;
background-color:black;
color: white;
box-shadow: 25px 25px 5px 2px rgb(7, 1, 28);
}

.Username {
  text-align: left;
  margin-left: 10px;
  display: flex;
  padding: 5px;
}
.Username label {
  flex: 1;
}
.Username input {
  flex: 3;
  margin-right: 10px;
  border-radius:5px;
}

.password {
  text-align: left;
  margin-left: 10px;
  display: flex;
  padding: 5px;
}

.password label {
  flex: 1;
}

.password input {
  flex: 3;
  margin-right: 10px;
  border-radius: 5px;
}
.body {
  background-color: darkolivegreen;
}